<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 基础表格
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-input v-model="keyword" placeholder="请输入关键字" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()">搜索</el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column prop="workId" label="工号" align="center"></el-table-column>
                <el-table-column label="全名" align="center">
                    <template slot-scope="scope">{{ scope.row.employee.fullname }}</template>
                </el-table-column>
                <el-table-column label="申请日期" align="center" prop="applyDate"></el-table-column>
                <el-table-column label="状态" align="center">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.status==='未审核'?'warn':(scope.row.status==='通过'?'success':(scope.row.status==='未通过'?'danger':'warn'))"
                        >{{ scope.row.status }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleEdit(scope.$index, scope.row)"
                        >审核
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="工号">
                    <el-input v-model="form.workId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="form.employee.fullname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="部门岗位">
                    <el-input v-model="form.office" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="离职原因">
                    <el-input v-model="form.reason" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="申请时间">
                    <el-input v-model="form.applyDate" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="申请材料">
                    <!--                    <el-input v-model="form.applyDoc" :disabled="true"></el-input>-->
                    <el-empty v-for="items in form.applyDocUrl" :key="items" :image="items" default=" " description=" "></el-empty>
<!--                    <h1 v-for="items in form.applyDocUrl">{{ items }}</h1>-->
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="danger" @click="saveEdit('未通过')">未通过</el-button>
                <el-button type="success" @click="saveEdit('通过')">通 过</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {getRequest} from "../../util/api";

export default {
    name: "resignCheck",
    data() {
        return {
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            keyword: null,
            tableData: [],
            multipleSelection: [],
            delList: [],
            editVisible: false,
            addVisible: false,
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            form: {
                resignId: "",
                workId: "",
                fullname: "",
                office: "",
                status: "",
                reason: "",
                applyDate: "",
                applyDoc: [],
                applyDocUrl: [],
                employee: {
                    fullname: ""
                }
            },
            idx: -1,
            id: -1,
        };
    },
    mounted() {
        this.getData();
    },
    methods: {
        // 获取 easy-mock 的模拟数据
        getData() {
            //console.log(this.$store.state.user.roleId)
            let url = 'oasys-back-end/getApply?page=' + this.page + '&size=' + this.size + '&roleId=' + this.$store.state.user.roleId;
            if (this.keyword != null) {
                url += '&keyword=' + this.keyword
            }
            getRequest(url).then(resp => {
                if (resp != null) {
                    this.tableData = resp.data.data
                    console.log(this.tableData)
                }

                console.log(this.tableData)
                this.total = resp.data.total;
                this.pageTotal = Math.ceil(this.total / this.size)
            })
        },
        // 删除操作
        handleDelete(index, row) {
            // 二次确认删除
            this.$confirm('确定要删除吗？', '提示', {
                type: 'warning'
            }).then(() => {
                let employee = row
                this.deleteRequest('oasys-back-end/deleteUser', employee).then(resp => {
                    if (resp) {
                        this.$message.success('删除成功');
                        this.tableData.splice(index, 1);
                    } else {
                        this.$message.error('删除失败');
                    }
                })
            })
                .catch(() => {
                });
        },
        // 多选操作
        handleSelectionChange(val) {
            this.multipleSelection = val;
            //console.log(this.multipleSelection.form)
        },
        delAllSelection() {
            const length = this.multipleSelection.length;
            let str = '';
            this.delList = this.delList.concat(this.multipleSelection);
            for (let i = 0; i < length; i++) {
                console.log(this.delList[i].workId)
                this.deleteRequest('oasys-back-end/deleteUser', this.delList[i]).then(resp => {
                    if (resp) {
                        str += this.delList[i].workId + ' ';
                    }
                })
                this.$message.error(`删除了` + str);
                this.multipleSelection = [];
                this.getData()
            }


        },
        // 编辑操作
        handleEdit(index, row) {
            this.idx = index;
            this.form = row;
            console.log("test=" + this.form.applyDoc)
            if (this.form.applyDoc!=null){
                this.form.applyDocUrl = this.form.applyDoc.split(',')
                console.log("number=" + this.form.applyDocUrl.length)
                //console.log("applyDoc=" + this.form.applyDoc.length)
                this.form.applyDocUrl.forEach((item, index) => {
                    this.form.applyDocUrl[index] = 'http://localhost:8080/oasys-back-end/documents/resignApply/' + this.form.resignId + '/' + item


                })
            }


            //console.log("test2=" + this.form.applyDocUrl.length)
            this.editVisible = true;
        },
        // 保存编辑
        saveEdit(params) {
            this.form.status = params
            if (this.form.status === '通过') {
                this.postRequest('oasys-back-end/applyResign', this.form).then(resp => {
                    if (resp) {
                        this.editVisible = false;
                        this.$message.success("修改成功")
                        this.getData()
                    } else {
                        this.$message.error("修改失败")
                    }
                })
            } else {
                this.postRequest('oasys-back-end/applyResign', this.form).then(resp => {
                    if (resp) {
                        this.editVisible = false;
                        this.$message.success("修改成功")
                        this.getData()
                    }
                })
            }
        },
        // 分页导航
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



