<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 基础表格
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-button
                    type="primary"
                    icon="el-icon-circle-plus"
                    class="handle-del mr10"
                    @click="normalCheckOn()"
                >日常考勤
                </el-button>
                <el-input v-model="keyword" placeholder="请输入关键字" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()">搜索</el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column label="工号" align="center">
                    <template slot-scope="scope">{{ scope.row.workId }}</template>
                </el-table-column>
                <el-table-column label="全名" align="center">
                    <template slot-scope="scope">{{scope.row.employee.fullname}}</template>
                </el-table-column>
                <el-table-column label="考勤时间" align="center">
                    <template slot-scope="scope">{{ scope.row.checkOnTime }}</template>
                </el-table-column>
                <el-table-column label="是否迟到" width="200" align="center">
                    <template slot-scope="scope">{{scope.row.isLate}}</template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "checkOn",
    data(){
        return{
            checkOn:{
                workId: this.$store.state.user.workId,

            },
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            tableData: [],
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            keyword: null
        }
    },
    mounted() {
        this.getData()
    },
    methods:{
        getData(){
            let url = "oasys-back-end/getAllCheckOnByWorkId?page="+this.page+"&size="+this.size+"&workId="+this.$store.state.user.workId
            this.getRequest(url).then(resp=>{
                if (resp.data.data != null){
                    this.tableData = resp.data.data;
                    this.total = resp.data.total;
                    this.pageTotal = Math.ceil(this.total / this.size)
                }
            })
        },
        normalCheckOn(){
            this.postRequest("oasys-back-end/normalCheckOn",this.checkOn).then(resp=>{
                console.log("resp="+resp.data)
                if (resp.data === true){
                    this.$message.success("签到成功")
                }else {
                    this.$message.warning("签到失败")
                }
            })
        },
        // 分页导航
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            //console.log(this.multipleSelection.form)
        },
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



