<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 基础表格
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-input v-model="keyword" placeholder="请输入关键字" clearable prefix-icon="el-icon-search"
                          class="handle-input"></el-input>
                <el-button type="primary" @click="getData()">搜索</el-button>
            </div>
            <el-table
                :data="tableData"
                border
                class="table"
                ref="multipleTable"
                header-cell-class-name="table-header"
                @selection-change="handleSelectionChange"
            >
                <el-table-column type="selection" width="55" align="center"></el-table-column>
                <el-table-column label="工号" align="center">
                    <template slot-scope="scope">{{ scope.row.workId }}</template>
                </el-table-column>
                <el-table-column label="全名" align="center">
                    <template slot-scope="scope">{{ scope.row.employee.fullname }}</template>
                </el-table-column>
                <el-table-column label="申请时间" align="center">
                    <template slot-scope="scope">{{ scope.row.applyDate }}</template>
                </el-table-column>
                <el-table-column label="薪资" align="center">
                    <template slot-scope="scope">{{ scope.row.employee.money }}</template>
                </el-table-column>
                <el-table-column label="申请涨薪至" align="center">
                    <template slot-scope="scope">{{ scope.row.increasingMoney }}</template>
                </el-table-column>
                <el-table-column label="上级审批状态" align="center">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.applyStatus==='通过'?'success':(scope.row.applyStatus==='未通过'?'danger':'')"
                        >{{ scope.row.applyStatus }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="财务部门审批状态" align="center">
                    <template slot-scope="scope">
                        <el-tag
                            :type="scope.row.moneyOfficeStatus==='通过'?'success':(scope.row.moneyOfficeStatus==='未通过'?'danger':'')"
                        >{{ scope.row.moneyOfficeStatus }}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180" align="center">
                    <template slot-scope="scope">
                        <el-button v-if="scope.row.moneyOfficeStatus === '未审核' "
                                   type="text"
                                   icon="el-icon-edit"
                                   @click="handleEdit(scope.$index, scope.row)"
                        >详情
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                    background
                    layout="prev, pager, next ,->, total"
                    :page-count="pageTotal"
                    :current-page="query.pageIndex"
                    :page-size="query.size"
                    @current-change="handlePageChange"
                    @size-change="sizeChange"
                    @prev-click="handlePageChange"
                    @next-click="handlePageChange"
                ></el-pagination>
            </div>
        </div>

        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="30%">
            <el-form ref="form" :model="form" label-width="70px">
                <el-form-item label="工号">
                    <el-input v-model="form.workId" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="全名">
                    <el-input v-model="form.employee.fullname" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="部门岗位">
                    <el-input v-model="form.office" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="原薪资">
                    <el-input v-model="form.employee.money" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="涨薪后薪资">
                    <el-input v-model="form.increasingMoney" :disabled="true"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button type="danger" @click="saveEdit('未通过')">未通过</el-button>
                <el-button type="success" @click="saveEdit('通过')">通 过</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {getRequest} from "../../util/api";

export default {
    name: "increasingMoneyOfficeCheck",
    data() {
        return {
            query: {
                address: '',
                name: '',
                pageIndex: 1,
                size: 0
            },
            keyword: null,
            tableData: [],
            multipleSelection: [],
            delList: [],
            editVisible: false,
            addVisible: false,
            pageTotal: 0,
            total: 0,
            size: 5,
            page: 1,
            form: {
                workId: this.$store.state.user.workId,
                office: "",
                applyStatus: "",
                moneyOfficeStatus: "",
                applyDate: "",
                increasingMoney: "",
                employee: {
                    fullname: "",
                    money: ""
                }
            },
            idx: -1,
            id: -1,
        };
    },
    mounted() {
        this.getData();
    },
    methods: {
        // 获取 easy-mock 的模拟数据
        getData() {
            //console.log(this.$store.state.user.roleId)
            let url = 'oasys-back-end/getAllPassApply?page=' + this.page + '&size=' + this.size;
            if (this.keyword != null) {
                url += '&keyword=' + this.keyword
            }
            getRequest(url).then(resp => {
                if (resp!=null){
                    this.tableData = resp.data.data
                    console.log(this.tableData)
                }

                console.log(this.tableData)
                this.total = resp.data.total;
                this.pageTotal = Math.ceil(this.total / this.size)
            })
        },
        // 多选操作
        handleSelectionChange(val) {
            this.multipleSelection = val;
            //console.log(this.multipleSelection.form)
        },
        // 编辑操作
        handleEdit(index, row) {
            this.idx = index;
            this.form = row;
            this.editVisible = true;
        },
        // 保存编辑
        saveEdit(params) {
            this.form.moneyOfficeStatus = params
            if (this.form.moneyOfficeStatus === '通过'){
                this.form.employee.money = this.form.increasingMoney
                this.postRequest('oasys-back-end/editMoneyOfficeStatus',this.form).then(resp=>{
                    if (resp.data===true){
                        this.editVisible = false;
                        this.$message.success("修改成功")
                        this.getData()
                    }else {
                        this.$message.error("修改失败")
                    }
                })
            }else {
                this.postRequest('oasys-back-end/editMoneyOfficeStatus',this.form).then(resp=>{
                    if (resp.data===true){
                        this.editVisible=false;
                        this.$message.success("修改成功")
                        this.getData()
                    }
                })
            }
        },
        // 分页导航
        handlePageChange(currentPage) {
            this.page = currentPage;
            this.getData();
        },
        sizeChange(currentSize) {
            this.size = currentSize;
            this.getData();
        },
    }
}
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 300px;
    display: inline-block;
}

.table {
    width: 100%;
    font-size: 14px;
}

.red {
    color: #ff0000;
}

.mr10 {
    margin-right: 10px;
}

.table-td-thumb {
    display: block;
    margin: auto;
    width: 40px;
    height: 40px;
}
</style>



