<template>
    <div>
        <div class="container">
            <div class="form-box">
                <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label="工号">
                        <el-input v-model="form.workId" :disabled="true">{{form.workId}}</el-input>
                    </el-form-item>
                    <el-form-item label="姓名">
                        <el-input v-model="form.fullname" >{{form.fullname}}</el-input>
                    </el-form-item>
                    <el-form-item label="申请时间">
                        <el-date-picker
                            v-model="form.applyDate"
                            type="datetime"
                            format="yyyy-MM-dd HH:mm:ss"
                            value-format="yyyy-MM-dd HH:mm:ss"
                            placeholder="选择日期">
                        </el-date-picker>
                    </el-form-item>
                    <el-form-item label="部门岗位">
                        <el-select v-model="form.office" placeholder="请选择">
                            <el-option key="002" label="行政岗位" value="001"></el-option>
                            <el-option key="007" label="人事部门员工" value="002"></el-option>
                            <el-option key="008" label="销售部门员工" value="003"></el-option>
                            <el-option key="009" label="财务部门员工" value="004"></el-option>
                            <el-option key="010" label="美术部门员工" value="005"></el-option>
                            <el-option key="011" label="策划部门员工" value="006"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="辞职原因">
                        <el-input type="textarea" rows="5" v-model="form.reason"></el-input>
                    </el-form-item>
                    <el-form-item label="申请附件">
                        <el-upload
                            class="upload-demo"
                            action=""
                            ref="upload"
                            :multiple="true"
                            name="file"
                            :on-preview="handlePreview"
                            :on-remove="handleRemove"
                            :on-change="handleChange"
                            :file-list="applyDoc"
                            :auto-upload="false">
                            <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
<!--                            <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>-->
                        </el-upload>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">表单提交</el-button>
                        <el-button>取消</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'baseform',
    data() {
        return {
            form: {
                workId: this.$store.state.user.workId,
                office: '',
                fullname: this.$store.state.user.fullname,
                reason: '',
                applyDate: '',
                applyDoc:''

            },
            applyDoc: []
        };
    },
    methods: {
        onSubmit() {
            // console.log(this.form.applyDoc)
            // let form = new FormData()
            // console.log("applyDoc="+this.applyDoc)
            // console.log(this.applyDoc)
            // form.append('file',this.applyDoc);
            // form.append('body',JSON.stringify(this.form))
            // console.log(this.form.applyDoc)
            //let i = 0
            // console.log(this.applyDoc.length)
            // form.append('file',JSON.stringify(params.applyDoc))
            // console.log(form.get('file'))
            // form.append('body',JSON.stringify(this.form))
            // console.log(form.length)


            let formData = new FormData()
            this.applyDoc.forEach(file=>{
                formData.append("file",file.raw)
                console.info(file.raw);
                this.form.applyDoc += file.raw.name;
                this.form.applyDoc += ','
            })
            this.form.applyDoc = this.form.applyDoc.substring(0,this.form.applyDoc.lastIndexOf(","))
            console.log(this.form.applyDoc)
            formData.append("form",JSON.stringify(this.form))
            this.fileRequest('oasys-back-end/resignApply',formData).then(resp=>{
                console.log(resp)
                if (resp.data===true){
                    this.$message.success('提交成功！');
                }else {
                    this.$message.error("提交失败")
                }
            })


        },
        onFileSubmit(){
            this.fileRequest('oasys-back-end/resignApply',this.form.applyDoc).then(resp=>{
                if (resp){
                    this.$message.success("上传成功")
                }else {
                    this.$message.error("上传失败")
                }
            })
        },
        handlePreview(file){
            console.log("file="+file)
        },
        handleRemove(file,applyDoc){
            console.log(file,applyDoc)
        },
        handleChange(file,applyDoc){
            this.applyDoc = applyDoc
            console.log(this.applyDoc)
        }
    }
};
</script>



